import React, { useContext } from "react";
import { FireContext } from "../FirePolicy";
import {
  FormControl,
  Select,
  MenuItem,
  TextField,
  Divider,
  Button,
} from "@mui/material";

export default function FirePolicyPremium() {
  const {
    state,
    handleInputChange,
    data,
    computation,
    isAddOrEditMode,
  } = useContext(FireContext);

  return (
    <div style={{ display: "flex" }}>
      <div
        style={{
          display: "flex",
          columnGap: "10px",
          flex: 1,
          flexDirection: "column",
        }}
      >
        <fieldset
          style={{
            display: "flex",
            flexDirection: "column",
            rowGap: "10px",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
            flex: 1,
          }}
        >
          <legend>Mortgage</legend>
          <FormControl
            size="small"
            sx={{
              width: "100%",
              maxWidth: "500px",
            }}
            required
            disabled={isAddOrEditMode}
          >
            <Select
              id="Mortgagee_id"
              labelId="Mortgagee"
              value={state.mortgage}
              name="mortgage"
              onChange={handleInputChange}
            >
              {[{ Mortgagee: "" }, ...data.data.firePolicy.mortgagee].map(
                (items: any, idx: number) => {
                  return (
                    <MenuItem key={idx} value={items.Mortgagee}>
                      {items.Mortgagee}
                    </MenuItem>
                  );
                }
              )}
            </Select>
          </FormControl>
        </fieldset>
        <fieldset
          style={{
            display: "flex",
            flexDirection: "column",
            rowGap: "10px",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
            flex: 1,
          }}
        >
          <legend>Warranties</legend>
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            name="warranties"
            value={state.warranties}
            multiline
            rows={5}
            onChange={(e) => {
              handleInputChange(e);
            }}
          />
        </fieldset>
      </div>
      <fieldset
        style={{
          display: "flex",
          flexDirection: "column",
          rowGap: "10px",
          padding: "15px",
          border: "1px solid #cbd5e1",
          borderRadius: "5px",
        }}
      >
        <legend>Premiums</legend>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            padding: "5px",
          }}
        >
          <Button
            disabled={isAddOrEditMode}
            size="small"
            variant="contained"
            color="primary"
            onClick={() => {
              computation()
            }}
          >
            Compute
          </Button>
        </div>

        <div
          style={{
            display: "grid",
            gridTemplateColumns: "repeat(2,1fr)",
            gap: "10px",
          }}
        >
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Insured Value"
            name="insuredValue"
            value={state.insuredValue}
            onChange={(e) => {
              handleInputChange(e);
            }}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Percentage"
            name="percentagePremium"
            value={state.percentagePremium}
            onChange={(e) => {
              handleInputChange(e);
            }}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Total Premium"
            name="totalPremium"
            value={state.totalPremium}
            onChange={(e) => {
              handleInputChange(e);
            }}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Vat"
            name="vat"
            value={state.vat}
            onChange={(e) => {
              handleInputChange(e);
            }}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Doc Stamp"
            name="docStamp"
            value={state.docStamp}
            onChange={(e) => {
              handleInputChange(e);
            }}
          />
          <div style={{ display: "flex", gap: "5px",position:"relative"}}>
            <TextField
              sx={{ width: "80px" }}
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              name="localGovTaxPercent"
              value={state.localGovTaxPercent}
              onChange={(e) => {
                handleInputChange(e);
              }}
            />
            <TextField
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Local Gov Tax"
              name="localGovTax"
              value={state.localGovTax}
              onChange={(e) => {
                handleInputChange(e);
              }}
            />
          </div>
        </div>
        <TextField
          disabled={isAddOrEditMode}
          required
          variant="outlined"
          size="small"
          label="F.S Tax"
          name="fsTax"
          value={state.fsTax}
          onChange={(e) => {
            handleInputChange(e);
          }}
        />
        <Divider color="secondary" />
        <TextField
          disabled={isAddOrEditMode}
          required
          variant="outlined"
          size="small"
          label="Total Due"
          name="totalDue"
          value={state.totalDue}
          onChange={handleInputChange}
        />
      </fieldset>
    </div>
  );
}
